.main-body {
    display: flex;
    flex-direction: column;
    background-color: #82acd6;
    min-height: 80rem;
  }
  
.body {
    display: flex;
    flex-direction: column; 
    font-size: larger;
    background-color: #82acd6;
    font-size: 24px;
}
  
input {
    width: 100%;
    padding: 5px 8px;
    margin: 4px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

form {
    width: 80%;
    margin-left: 10%;
    margin-right: 10;
    text-align: left;
}
  
  h2 {
      text-align: center;
      font-size: 1.5em;
      margin-top: 1.5em;
      margin-bottom: 5px;
  }
  
  h3 {
    text-align: left;
    font-size: 1.2em;
    margin-top: 1.2em;
    margin-bottom: 5px;
  }
  
  li {
      font-size: 1em;
      margin-top: 5px;
  }
  
  ul {
    list-style-type: none;
  }
  
  button {
   float: left;

  }
  
  button:hover {
    color: red;
    background-color: #9db8d3;
  }
  
  .general {
    text-align: center;
    position: relative;
  }